<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "",
      type: "home",
      timer2: null,
      isApprove: false,
      // wokt: "0",
      // Apy: 0,
      // isHome: true,
      stakeAmount: 0,
      // hylink: "0x4b26a8d7be71b2a221e468e9f9f05d9af9c9229b",
      link: "--",
      // isMaster: false,
      // isShowEstablish: false,
      // creationCode: "",
      // Approve: false,
      jion: "--",
      show: false,
      joinItem: {
        50: 50,
        100: 100,
        200: 200,
        300: 300,
        400: 400,
        // "500": 500,
      },
    };
  },
  computed: {
    ...mapState("main", [
      // "cuseInfo",
      // "cuseContract",
      "lpInfo",
      "lpContract",
      "account",
      // "cuseBalance",
      "lpBalance",
      // "cuseprice",
      "lpCoinContract",
      // "cuseCoinContract",
      // "TVLusdt",
      // "TVLcuse",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      // "add_cuseInfo",
      "add_lpInfo",
      "add_lpCoinContract",
      // "add_lpCoinContract",
    ]),
    cols: function () {
      this.show = false;
    },
    clearFun: function () {
      this.stakeAmount = 0;
      // this.tip = false;
    },
    max: function () {
      var num = this.lpBalance;
      // num = num.toString();
      // let index = num.indexOf(".");
      // if (index !== -1) {
      //   num = num.substring(0, 4 + index + 1);
      // } else {
      //   num = num.substring(0);
      // }
      num = num.split(".")[0];
      this.stakeAmount = num;
    },
    // 添加期权
    optionIn() {
      var _this = this;
      var account = _this.account;
      var a = 10000 * 10 ** 18;
      this.lpContract.methods
        .optionIn(
          "0x6FB32c80A361B9086669b6653F8566F21eB880DA",
          "0x6FB32c80A361B9086669b6653F8566F21eB880DA",
          _this.$route.query.invite,
          "10000000000000000000000"
        )
        .send({ from: account })
        .then((res) => {})
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    okFun: function () {
      if (this.lpInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      if (!_this.jion) {
        this.$toast(this.$t("message.dao"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = _this.account;
      this.lpContract.methods
        .bind(account, _this.jion)
        .send({ from: account })
        .then((res) => {
          // console.log("getReward==============", res);
          _this.$toast.clear();
          _this.$toast.success("OK");
          _this.add_lpInfo();
          // _this.getCoinBalance();
          _this.add_lpCoinContract();
          // _this.show=false;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getJoin: function (val) {
      var _this = this;

      if (this.stakeAmount == "--") {
        this.errTips(this.langItem.tips.sorry);
        return;
      }
      // this.stakeAmount = val;
      this.stakeAmount = Number(this.stakeAmount) + val;
      setTimeout(function () {
        _this.tip = false;
      }, 3000);
    },
    withdrawFun: function () {
      if (this.lpInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = _this.account;
      this.lpContract.methods
        .withdrawProfitEur()
        .send({ from: account })
        .then((res) => {
          // console.log("getReward==============", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_lpInfo();
          // _this.getCoinBalance();
          _this.add_lpCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.lpInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = _this.account;
      this.lpContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          // console.log("getReward==============", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_lpInfo();
          // _this.getCoinBalance();
          _this.add_lpCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //取消抵押
    // withdraw: function () {
    //   if (this.lpInfo == null) {
    //     this.$toast(this.$t("message.noMask"));
    //     return;
    //   }
    //   var _this = this;
    //   var account = _this.account;
    //   var myzy = web3.utils.fromWei(_this.lpInfo.info[3], "ether");
    //   if (myzy <= 0) {
    //     _this.$toast(_this.$t("message.noZy"));
    //     return;
    //   }
    //   this.$toast.loading({
    //     message: this.$t("message.pack"),
    //     duration: 0,
    //     overlay: true,
    //     closeOnClickOverlay: true,
    //   });
    //   this.lpContract.methods
    //     .exit()
    //     .send({ from: account })
    //     .then((res) => {
    //       console.log("withdraw=======", res);
    //       _this.$toast.clear();
    //       _this.$toast.success(_this.$t("message.dealSuccee"));
    //       _this.add_lpInfo();
    //       // _this.apyFun();
    //       // _this.add_lpCoinContract();

    //       _this.withdrawAmount = 0;
    //     })
    //     .catch((err) => {
    //       this.$toast.clear();
    //       this.$toast(err.message);
    //       console.log(err);
    //     });
    // },
    // 授权
    lpApprove: function () {
      if (this.lpInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.lpInfo.address;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.lpCoinContract.methods
        .approve(address, "1000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("授权=====", res);
          if (res.status) {
            this.$toast.success(this.$t("message.accredit"));
            this.isApprove = true;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      if (this.lpInfo) {
        var account = this.account;
        var address = this.lpInfo.address;
        console.log(address);
        this.lpCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a > 0) {
              this.isApprove = true;
            }
            console.log("a==========", a);
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
    stake: function () {
      if (this.lpInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var inviteCode = _this.lpInfo.inviteCode;
      var referrer = _this.lpInfo.referrer;
      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        var invite = _this.$route.query.invite;
        if (invite) {
          referrer = invite.toString();
        } else {
          // this.show = true;
          // return;
        }
      }

      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (Number(_this.stakeAmount) > Number(_this.lpBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) < 50) {
        _this.$toast(_this.$t("message.excess"));
        return;
      }
      // if (!inviteCode || inviteCode == "--") {
      //   _this.$toast(_this.$t("message.onId"));
      //   return;
      // }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      //  alert(inviteCode)
      //  alert( referrer)
      //  alert(web3.utils.toWei(_this.stakeAmount, "ether"))
      //  this.$toast(web3.utils.toWei(_this.stakeAmount, "ether"));
      console.log(
        inviteCode,
        referrer,
        web3.utils.toWei(_this.stakeAmount, "ether")
      );
      this.lpContract.methods
        .investIn(
          inviteCode,
          referrer,
          web3.utils.toWei(_this.stakeAmount, "ether")
        )
        .send({ from: account })
        .then((res) => {
          if (res.status) {
            _this.$toast.clear();
            _this.$toast.success(_this.$t("message.dealSuccee"));
            _this.add_lpInfo();
            _this.getUrl();
            // _this.apyFun();
            _this.add_lpCoinContract();
          }

          _this.stakeAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
      this.$router.push({
        name: this.$route.name,
        params: { lang: this.lang },
      });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.lpInfo) {
        if (this.lpInfo.referrer) {
          var addr = this.lpInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?invite=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(36, 42)
        : addr;
    },
  },
  mounted() {
    var _this = this;
    var lang = localStorage.getItem("lang");
    var newR = localStorage.getItem("r");
    this.changeLanguage(lang || "zhCHS");
    this.jion = this.$route.query.invite;
    // if (newR) {
    //   this.localR = newR;
    // }

    // this.$dialog.alert({
    //   title: this.$t("message.TipTitle"),
    //   message: this.$t("message.TipCont"),
    //   confirmButtonText: this.$t("message.btnText"),
    //   theme: "round-button",
    // });
    if (!window.ethereum) {
      this.$notify({ type: "danger", message: this.$t("message.noMask") });
      return;
    } else {
      var web3 = new Web3(window.ethereum);
      web3.eth.net.getId().then((id) => {
        if (id != 56) {
          this.$notify({
            type: "danger",
            message: "Please switch the network!",
          });
          return;
        }
      });
    }
    var getData = () => {
      this.add_lpInfo();
      this.add_lpCoinContract();
      // this.add_lpCoinContract();
      // this.apyFun();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.lpCoinContract || !this.lpContract || !this.lpInfo) {
          getData();
        } else {
          this.isApproveFun();
          // this.cuseIsApproveFun();
          getData();
          // console.log(this.lpInfo);
          // if (!_this.lpInfo.inviteCode && _this.$route.query.inviteCode) {
          //   _this.joinEstablish = _this.$route.query.inviteCode;
          // }
          if (!_this.lpInfo.referrer || _this.lpInfo.referrer == "--") {
            // this.show=true
          }
          if (_this.lpInfo.inviteCode) {
            var invite = _this.lpInfo.inviteCode;

            _this.getUrl();
          }

          // var oldArr = this.lpInfo.poolAddr.toLocaleLowerCase();
          // var hasIndexOf = oldArr.indexOf(this.account);
          // if (hasIndexOf == 0) {
          //   this.isMaster = true;
          // }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

