<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show:false,
      langList: [
        {
          text: "中文",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
    type: {
      type: String,
      default: "home",
    },
     isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "oktInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_oktInfo"]),
    showPopup(){
      this.show=true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(36, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
        tips(){
       this.$dialog.alert({
        message: this.$t("message.tips3"),
        theme: 'round-button',
      })
    },
     prev(){
      this.$router.go(-1);
       },
     
    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url) {
      console.log(url.length);
      if (url.length > 1) {
        this.$router.push({ path: url + "/" + this.lang });
      } else {
        this.$router.push({ path: url });
      }
    },
  },
};
</script>

